ul li {
  list-style: none;
}


#wrap {
  width: 15%;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 15px;
  overflow: hidden;
  font-family: 'NanumGothic', 'Noto Sans KR', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  line-height: 1.5;
  height: 72vh;
}

#wrap h2 {
  width: 100%;
  margin: 0;
}

#wrap h2 a {
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: #f5fefd;
  font-size: 20px;
}

#wrap p {
  display: none;
  margin: 15px 0;
  padding: 0 15px;
  font-size: 20px;
}

#wrap img {
  width: 280px;
  margin-top: 15px;
}

#wrap ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 15px 0;
  width: 280px;
}

#wrap ul li {
  margin: 5px 5px 0;
  padding: 5px 10px;
  border-radius: 5px;
  background: #222;
  color: #fff;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 14px;
  font-family: "Noto Sans", sans-serif;
}