* {
  padding: 0;
  margin: 0;
}



#loading_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: #000;
}

#container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background: #000;
  padding-top: 15px;
  clear: both;
  position: relative;
}

.nav {
  margin: 0;
  width: 100vw;
  height: 100px;
}

#loading_wrap > .loading {
  font-size: 48px;
  color: #fff;
  font-family: 'Noto Sans KR', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  animation-duration: 1.5s;
  animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  animation-name: move;
}

@keyframes move {
  0% {
    transform: translateX(-50px);
  }

  50% {
    transform: translateX(50px);
  }

  100% {
    transform: translateX(0px);
  }
}