#detail {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#detail .cover {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  background: #000;
  z-index: 0;
}

#detail .background {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}
#detail .contents {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  z-index: 1;
  font-family: "Roboto", "Noto Sans", sans-serif;
  line-height: 1.4;
} 
#detail .contents h1 {
  margin-top: 15px;
}
#detail .contents h1 a {
  text-decoration: none;
  color: #fff;
}
#detail .contents h2 {
  margin-top: 15px;
}
#detail .contents h2 span {
  display: inline-block;
  margin-right: 10px;
  padding: 5px 10px;
  color: #ccc;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 16px;
}

#detail .contents h2 em {
  display: block;
  text-decoration: none;
  margin-top: 15px;
  color: #fff;
  font-style: normal;
}

#detail .contents p {
  margin-top: 15px;
  color: #ccc;
  font-size: 18px;
}
